import './Date.css';

function Date() {
  return(
    <section className='date'>
      <h2 className='page__title date__title'>WHEN?</h2>
      <p className='date__week-day'></p>
      <div className='date__container'>
        <p className='date__month'></p>
        <p className='date__day'>04</p>
        <p className='date__time'>16:00</p>
      </div>
    </section>
  )
}

export default Date;